body {
  background-color: #fff8dc;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.search-form {
  background-color: #87ceeb;
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: flex;
}

input {
  border: 1px solid #ccc;
  border-radius: 4px;
  flex: 1;
  margin-right: 10px;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #00f;
  border: none;
  border-radius: 4px;
  margin-left: 10px;
  padding: 10px;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  padding: 20px 0;
  display: grid;
}

.photo-card {
  background-color: #87ceeb;
  border-radius: 8px;
  transition: transform .3s ease-in-out;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.photo-card:hover {
  transform: scale(1.05);
}

.photo-card img {
  width: 100%;
  height: auto;
}

.info {
  justify-content: center;
  align-items: center;
  gap: 20px;
  font-size: 12px;
  display: flex;
}

.info-item {
  flex-direction: column;
  display: flex;
}

